.App {
  width: 30vw;
  margin: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(5px);
  border-radius: 20px;
}
@media only screen and (min-width: 376px) and (max-width: 779px) {
  .App {
    width: 45vw;
  }
}
@media only screen and (max-width: 450px) {
  .App {
    width: 100vw;
    height: 100vh;
  }
}
